import React from "react"
import Image from "../components/image"

import styled, { keyframes } from "styled-components"

import { Link } from "gatsby"

const CONTAINER_HEIGHT = 60

const Container = styled.div`
  height: ${CONTAINER_HEIGHT}px;
`

const NavigationContainer = styled.div`
  display: flex;
  padding: 0 2em 0 0;
  height: ${({ shrink }) => (shrink ? 50 : CONTAINER_HEIGHT)}px;
  transition: height 1s, scale 1s, translate 1s;
  background: rgba(220, 220, 220, 0.7);
  // backdrop-filter: blur(2px);
  // opacity: 0.5;
  // position: sticky;
  // top: 0;
  z-index: 1000;
`

const rotate = keyframes`
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
`
const NavigationLogo = styled.div`
  padding: 0 1em;
  transition: transform 1s;

  ${({ shrink }) =>
    shrink &&
    `
        transform: scale(0.5) translate3d(0, -50%, 0);
    `};
`
const NavigationLogoSpinner = styled.div`
  height: 100px;
  width: 100px;
  min-width: 100px;
  border-radius: 100px 100px 100px 100px;
  overflow: hidden;
  // transition: transform 1s;
  // .gatsby-image-wrapper {
  //   position: absolute;
  // }
  transform-origin: center;
  animation: ${rotate} 12s infinite linear;
`
const NavigationList = styled.ul`
  list-style: none;
  width: 100%;
  height: 100%;
  // overflow: hidden;
  // height: ${({ shrink }) => (shrink ? 50 : 100)}%;
  transition: height 1s, scale 1s, translate 1s;

  margin: 0;

  display: flex;
`

const NavigationListItem = styled.li`
  font-style: italic;

  // border: 1px solid gray;
  // background: lightgray;
  height: 100%;
  width: 100%;

  margin: 0;
  padding: 0;

  text-transform: uppercase;
  font-weight: bold;
  transition: font-size, background 0.4s;

  &:hover {
    background: #ffffff;
    font-size: 105%;
  }

  a {
    color: #666666;
    text-decoration: none;
    padding: 0 1em;
    width: 100%;
    height: 100%;

    align-items: center;
    justify-content: left;
    display: flex;

    &:hover {
      color: #878787;
    }
  }
`

const SCROLL_THRESHOLD = 100

const Navigation = ({ links, shrink }) => {
  return (
    <Container>
      <NavigationContainer className="Navigation" shrink={shrink}>
        <NavigationLogo className="NavigationLogo" shrink={shrink}>
          <NavigationLogoSpinner className="NavigationLogoSpinner">
            <Image name="placeholderLogo" />
          </NavigationLogoSpinner>
        </NavigationLogo>
        <NavigationList shrink={shrink}>
          {links.map(item => {
            //
            return (
              <NavigationListItem>
                <Link to={item.href}>{item.label}</Link>
              </NavigationListItem>
            )
          })}
        </NavigationList>
      </NavigationContainer>
    </Container>
  )
}

export default Navigation
